body {
    line-height: 36px;
    font-family: 'Roboto', sans-serif;
    font-style  : italic;
    font-size: 24px; 
}

/* introduce */

.introduce {
    height: 100vh;
    background: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week2/banner.png?raw=true) center center;
    background-size: cover;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.name {
    line-height: 72px;
    font-size: 80px;
    font-weight: Bold;
}

.social-media {
    display: flex;
}

/* about */

.about {
    text-align: center;
}

.sub-title {
    font-size: 64px;
    line-height: 84px;
}

.about img {
    margin-bottom: 20px;
}

.line {
    width: 384px;
}

i {
    display: inline-block;
    width: 22px;
    height: 22px;
    background-color: #891818;
    border-radius: 50%;
    margin-right: 16px;
}

/* brand */

.brand {
    color: white;
    background: black;
    text-align: center;

    display: flex;
    justify-content: center;
    align-items: center;
}

.brand-image {
    width: 1200px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

/* other-works */
 
.other-works {

    display: flex;
    justify-content: center;
    text-align: center;
}

.work-list {
    width: 1200px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

/* contact */

.contact {
    color: white;
    background: black;
    text-align: center;
}